%ic {
  width: 14px;
  height: 14px;
  background: url("#{$url}/graphics/icon_vne.svg") no-repeat;
  display: inline-block;
  vertical-align: middle;
  /*margin-top: -4px;*/
}

/**DETAIL INLINE**/
