}

/**ARTICLE HEADER**/
.metadate {
  font: 400 px_to_rem(14px) arial;
  color: #999;
}

.title_news_detail {
  font: 700 px_to_rem(28px) $fontmain;
  line-height: $lh;
  color: #333;
  margin-bottom: 10px;

  span {
    background: #11affe;
    color: #fff;
    font-size: 13px;
    height: 20px;
    line-height: 20px;
    padding: 0 8px;
    float: left;
    margin: 8px 10px 0 0;
    border-radius: 2px;
    -webkit-border-radius: 2px;
    -moz-border-radius: 2px;
  }
}

.count_share_total {
  .count_share {
    display: inline-block;
    padding: 0 10px;
    font: 400 14px/28px arial;
    color: #999;
    background: #f6f6f6;
    border-radius: 3px;
    position: relative;
  }

  .count_share::before {
    position: absolute;
    left: -5px;
    top: 50%;
    margin-top: -5px;
    content: "";
    border-right: 5px solid #eee;
    border-top: 5px solid transparent;
    border-bottom: 5px solid transparent;
  }
}

.luu_tin a {
  .ic-bookmark {
    position: relative;
  }
}

.luu_tin.active {
  a {
    .ic-bookmark {
      background-position: -99px -126px;
    }
  }
}

.luu_tin {

  /*margin-left: 10px;*/
  a {
    margin-right: 15px;
    height: 32px;
    line-height: 29px !important;
    border: 1px solid #cdcdcd;
    background: none;
    border-radius: 50%;
    display: block;
    text-align: center;
  }
}



#medium_editor {
