.embed-container {
  position: relative;
  height: 0;
  overflow: hidden;
  width: 100%;
  float: left;
  padding-bottom: 56.25%;
  margin-bottom: 15px;

  iframe {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    border: none;
  }

  &.audio {
    padding-bottom: 19%;
    width: 100%;
  }

  &.embed-container-square {
    padding-bottom: 100%;
  }
}

.g-before-after {
  position: relative;
  overflow: hidden
}

.g-before-after img {
  display: block
}

.g-before-after .g-img-before {
  float: left
}

.g-before-after .g-img-after {
  left: 50%;
  bottom: 0;
  overflow: hidden
}

.g-before-after .g-img-after,
.g-before-after .g-img-after img {
  position: absolute;
  right: 0;
  top: 0
}

.g-before-after .g-img-divider {
  position: absolute;
  left: 50%;
  top: 0;
  bottom: 0;
  width: 0;
  border-left: 1px solid #fff;
  border-right: 1px solid #fff
}

.g-before-after .g-img-after img {
  max-width: inherit;
}

.g-before-after .g-img-divider span {
  position: absolute;
  top: 50%;
  display: block;
  line-height: 1;
  text-align: center;
  -webkit-transform: translate(-50%, -50%);
  -ms-transform: translate(-50%, -50%);
  transform: translate(-50%, -50%);
  height: 26px;
  width: 26px;
  color: transparent;
  border-radius: 50%;
  -moz-border-radius: 50%;
  -webkit-border-radius: 50%;
}

