/*Live container*/
.container_tab_live {
  ul {
    li.block {
      float: left;
      width: 100%;
      padding-bottom: 15px;
      /*padding-top: 15px;  */
      position: relative;

      /*border-left: 1px solid #eee;
        padding-left: 15px;*/
      /*&::before{width: 8px; height: 8px; background: #eeeeee; content: ''; position: absolute; top: 3.5px; left: -5px; -webkit-border-radius: 50%;
          -moz-border-radius: 50%;
          -ms-border-radius: 50%;
          -o-border-radius: 50%;
          border-radius: 50%;}*/
      &::after {
        width: 100%;
        height: 1px;
        background: #e6e6e6;
        content: '';
        position: absolute;
        bottom: 15px;
        left: 0;
      }

      &:last-child,
      &.end {
        margin-bottom: 0;
        padding-bottom: 0;

        &::after {
          display: none;
        }

        /*&::before{width: 100%; height: 1px; background: #e6e6e6; content: ''; position: absolute; top: 0; left: 0;}
          &:first-child,&.end{padding-top: 0;
            &::before{display: none;}*/
      }

      div.minutes {
        float: left;
        width: 100%;
        position: relative;
        padding: 0;
        margin: 0 0 20px;

        .text_minutes {
          display: inline-block;
          text-align: center;
          float: left;
          font-size: px_to_rem(16px);
          color: $colormain;
          margin-right: 10px;

          &.special_time {
            background: $colormain;
            color: #fff;
            padding: 2px 5px;
            border-radius: 4px;
          }
        }

        .icon_live {
          display: none !important;
        }
      }

      .text_live {
        font: 400 px_to_rem(18px) arial;
        line-height: $lh;
      }

      .day_time {
        font-weight: 700;
        color: $colormain;
        margin-bottom: 10px;
      }
    }
  }
}

