  &.quiz_box_multimark {
    .ans_item {
      a {
        padding-left: 45px;

        &:before {
          border: 1px solid #E5E5E5;
          background: #fff;
          width: 16px;
          height: 16px;
          position: absolute;
          top: 9px;
          left: 15px;
          border-radius: 2px;
          content: '';
